<template>
  <div class="dialogSet">
    <el-button :disabled="dialogSetDisabled" :type="type" @click="dialogFormVisible = true" :size="size">{{title}}</el-button>
    <el-dialog :close-on-click-modal='false' :close-on-press-escape='false' :title="title" :visible.sync="dialogFormVisible" @close="closeForm"  width="500px" top="0">
      <el-form :model="form" :rules="rules" ref="form" size="small" :label-width="formLabelWidth" label-position="left">
        <el-form-item label="精选分类" prop="role">
          <el-select :clearable="true" v-model.trim="form.role" placeholder="请选择精选分类" style="width:100%">
            <el-option label="专享优惠" value="1"></el-option>
            <el-option label="情人节专享" value="2"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeForm">取 消</el-button>
        <el-button size="small" type="primary" @click="submitForm('form')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: 'DialogSet',
  props: {
    title: {
      type: String
    },
    size: {
      type: String,
      default: 'small'
    },
    type: {
      type: String,
      default: 'primary'
    },
    dialogSetDisabled: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      dialogFormVisible: false,
      form: {
        role: ''
      },
      formLabelWidth: '79px',
      rules: {
        role: [
          { required: true, message: '请选择精选分类', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.dialogFormVisible = false
          this.$message({
            type: 'success',
            message: '产品设为精选成功!'
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 关闭dialog
    closeForm () {
      this.$refs['form'].resetFields()
      this.dialogFormVisible = false
    }
  }
}
</script>
<style lang='stylus' ref='stylesheet/stylus'>
.dialogSet
  display inline-block
  margin 0 10px
</style>



