<template>
  <div class="productUpperMg">
    <el-form :inline="true" :model="searchData" ref="searchData" class="demo-form-inline" size='small' label-width="68px">
      <el-form-item label="产品编号" prop="productNumber">
        <el-input v-model.trim="searchData.productNumber" placeholder="请输入产品编号" clearable></el-input>
      </el-form-item>
      <el-form-item label="产品名称" prop="productName">
        <el-input v-model.trim="searchData.productName" placeholder="请输入产品名称" clearable></el-input>
      </el-form-item>
      <el-form-item label="产品分类" prop="productClass">
        <el-select v-model.trim="searchData.productClass" placeholder="请选择产品分类" clearable>
          <el-option label="全部" value="0"></el-option>
          <el-option label="为你推荐" value="1"></el-option>
          <el-option label="鲜花系列" value="2"></el-option>
          <el-option label="干花系列" value="3"></el-option>
          <el-option label="礼品系列" value="4"></el-option>
          <el-option label="绿植多肉" value="5"></el-option>
          <el-option label="花器耗材" value="6"></el-option>
          <el-option label="专属定制" value="7"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="产品品牌" prop="productBrand">
        <el-select v-model.trim="searchData.productBrand" placeholder="请选择产品品牌" clearable>
          <el-option label="全部" value="0"></el-option>
          <el-option label="HTF_HZH" value="1"></el-option>
          <el-option label="NIUBINHJ" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="栏目" prop="productColumn">
        <el-select v-model.trim="searchData.productColumn" placeholder="请选择栏目" clearable>
          <el-option label="全部" value="0"></el-option>
          <el-option label="精选" value="1"></el-option>
          <el-option label="选购" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="状态" prop="productState">
        <el-select v-model.trim="searchData.productState" placeholder="请选择状态" clearable>
          <el-option label="未上架" value="0"></el-option>
          <el-option label="已上架" value="1"></el-option>
          <el-option label="已下架" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="剩余库存" prop="productStock">
        <el-select v-model.trim="searchData.productStock" placeholder="请选择剩余库存" clearable>
          <el-option label="全部" value="0"></el-option>
          <el-option label="0" value="1"></el-option>
          <el-option label="非0" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="价格" prop="productPrice">
        <el-select v-model.trim="searchData.productPrice" placeholder="请选择价格" clearable>
          <el-option label="升序" value="0"></el-option>
          <el-option label="降序" value="1"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary">查询</el-button>
        <el-button @click="resetForm('searchData')">重置</el-button>
      </el-form-item>
    </el-form>
    <div class="divisionLine"></div>
    <!-- 表格按钮 -->
    <div class="tableBtn">
      <el-button type="primary" size="small" @click="addProduct('add')">新增</el-button>
      <el-button type="danger" size="small" :disabled="multipleSelection.length===0" @click="delProduct">删除</el-button>
      <el-button type="primary" size="small" :disabled="multipleSelection.length===0" @click="upperShelf">上架</el-button>
      <el-button type="danger" size="small" :disabled="multipleSelection.length===0" @click="lowerShelf">下架</el-button>
      <DiaLogSet title="设为精选" :dialogSetDisabled="multipleSelection.length===0"/>
      <el-button type="danger" size="small" :disabled="multipleSelection.length===0" @click="cancelSelected">取消精选</el-button>
    </div>
    <!-- 表格 -->
    <div class="tableContainer">
      <el-table
        size="medium"
        border
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          label-class-name="checkboxSelect"
          align="center">
        </el-table-column>
        <el-table-column
          prop="productNumber"
          label="产品编号"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="productName"
          label="产品名称"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="productClass"
          label="产品分类"
          align="center"
          width="77"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="productBrand"
          label="产品品牌"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="productStock"
          label="库存数量"
          align="center"
          width="77"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="productOverStock"
          label="剩余库存"
          align="center"
          width="77">
        </el-table-column>
        <el-table-column
          prop="productPrice"
          label="价格"
          align="center"
          width="77"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="productColumn"
          label="栏目"
          align="center"
          width="83"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="productState"
          label="状态"
          align="center"
          width="63"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          label="操作"
          align="center"
          width="230">
          <template slot-scope="scope">
            <!-- <el-button
              size="mini"
              type="primary"
              @click="handleEdit(scope.$index, scope.row)">修改</el-button> -->
              <el-button type="primary" size="mini"  @click="addProduct('edit')">修改</el-button>
              <el-button size="mini"  @click="addProduct('detail')">详情</el-button>
              <el-button size="mini" @click="addProduct('statistics')">统计</el-button>
            <!-- <el-button
              size="mini"
              @click="handleDetail(scope.$index, scope.row)">详情</el-button> -->
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[10, 20, 30]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="2">
    </el-pagination>
  </div>
</template>
<script>
import DiaLogSet from './DialogSet'
export default {
  name: 'ProductUpperMg',
  data () {
    return {
      searchData: {
        productNumber: '',
        productName: '',
        productClass: '',
        productBrand: '',
        productColumn: '',
        productState: '',
        productStock: '',
        productPrice: ''
      },
      tableData: [{
        productNumber: 'FL201810170001',
        productName: '产品套餐1',
        productClass: '为你推荐',
        productBrand: 'THE BEAST',
        productStock: '9999',
        productOverStock: '8888',
        productPrice: '1,314.00',
        productColumn: '精选/选购',
        productState: '已上架'
      }, {
        productNumber: 'FL201810170001',
        productName: '产品套餐12',
        productClass: '为你推荐',
        productBrand: 'THE BEAST',
        productStock: '9999',
        productOverStock: '8888',
        productPrice: '1,314.00',
        productColumn: '精选/选购',
        productState: '已下架'
      }, {
        productNumber: 'FL201810170001',
        productName: '产品套餐12',
        productClass: '为你推荐',
        productBrand: 'URBAN GREEN MAKERS',
        productStock: '9999',
        productOverStock: '8888',
        productPrice: '1,314.00',
        productColumn: '精选/选购',
        productState: '未上架'
      }],
      currentPage: 2,
      multipleSelection: []
    }
  },
  components: {
    DiaLogSet
  },
  methods: {
    // 新增
    addProduct (arg) {
      if (arg === 'add') {
        this.$router.push('/addProduct')
      } else if (arg === 'detail') {
        this.$router.push({
          path: `/productDetail`
        })
      } else if (arg === 'edit') {
        this.$router.push({
          path: `/productDetail`
        })
      } else {
        this.$router.push('/statistics')
      }
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    handleSelectionChange (val) {
      this.multipleSelection = val
    },
    // 取消精选
    cancelSelected () {
      this.$confirm('您确定要把被选中的产品<span class="red">取消精选</span>吗？', {type: 'warning', dangerouslyUseHTMLString: true})
        .then(_ => {
          this.$message({
            type: 'success',
            message: '产品取消精选成功!'
          })
        })
        .catch(_ => {})
    },
    // 下架
    lowerShelf () {
      this.$confirm('您确定要<span class="red">下架</span>被选中的产品吗？', {type: 'warning', dangerouslyUseHTMLString: true})
        .then(_ => {
          this.$message({
            type: 'success',
            message: '产品下架成功!'
          })
        })
        .catch(_ => {})
    },

    // 上架
    upperShelf () {
      this.$confirm('您确定要<span class="red">上架</span>被选中的产品吗？', {type: 'warning', dangerouslyUseHTMLString: true})
        .then(_ => {
          this.$message({
            type: 'success',
            message: '产品上架成功!'
          })
        })
        .catch(_ => {})
    },
    // 删除
    delProduct () {
      this.$confirm('您确定要<span class="red">删除</span>被选中的产品吗？', {type: 'warning', dangerouslyUseHTMLString: true})
        .then(_ => {
          this.$message({
            type: 'success',
            message: '产品删除成功!'
          })
        })
        .catch(_ => {})
    },
    // 分页
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
    }
  }
}
</script>
<style lang='stylus' ref='stylesheet/stylus'>
.productUpperMg
  .tableBtn
    margin 18px 0
  .tableContainer
    .el-table-column--selection
      .cell
        padding-right 10px!important
  .el-pagination
    padding 0px!important
    text-align right
    margin-top 18px
</style>



